import React, {Component} from 'react'
import { connect } from 'react-redux'
import { withStyles } from '@material-ui/core/styles'

import Typography from '@material-ui/core/Typography'
import LinearProgress from '@material-ui/core/LinearProgress'

const styles = () => ({
  root: {
    position: 'fixed',
    top: '5rem',
    right: '2rem',
    width: '10rem',
    height: '2rem'
  },
  title: {
    color: '#fff',
    marginBottom: '.5rem',
    letterSpacing: '.5rem'
  },
  life: {
    height: '1rem'
  }
})

class LifeContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const {classes, life} = this.props

    return (
      <div className={classes.root}>
        <Typography variant='subheading' component='p' className={classes.title}>
          LIFE
        </Typography>
        <LinearProgress
          variant='determinate'
          value={life}
          color={life < 50 ? 'secondary' : 'primary'}
          className={classes.life} />
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    life: state.life
  }
}

export default connect(mapStateToProps)(withStyles(styles)(LifeContent))



