import React, {Component} from 'react'
import {Link, withRouter} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../constant'
import {getRandomNumber} from '../../util/random'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

import UserContent from './Role/User/UserContent'
import LifeContent from './Life/LifeContent'
import KillContent from './Kill/KillContent'

import AngleContent from './Role/Enemy/Nobody/AngleContent'
import RectangleContent from './Role/Enemy/Nobody/RectangleContent'
import BossContent from './Role/Enemy/Boss/BossContent'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  bossRoad: {
    display: 'inline-block',
    border: '2px solid rgba(255, 255, 255, .5)',
    borderRadius: '50%',
    position: 'fixed',
    top: '30px',
    left: 'calc(50vw - 50vh + 30px)',
    width: 'calc(100vh - 60px)',
    height: 'calc(100vh - 60px)'
  },
  message: {
    margin: '10rem',
    textAlign: 'center'
  }
})

class GameContent extends Component {
  constructor () {
    super()
    this.state = {
      cubeRange: {
        x: document.documentElement.clientWidth,
        y: document.documentElement.clientHeight
      },
      createEnemy: null
    }
  }

  componentWillReceiveProps () {
    const {isGameOver} = this.props
    if (isGameOver) {
      const {addNewRank, player, totalKill} = this.props
      clearInterval(this.state.createEnemy)
      player && addNewRank({
        player,
        totalKill,
        timestamp: Date.now()
      })
    }
  }

  componentDidMount () {
    const {player, history} = this.props

    if (!player) {
      clearInterval(this.state.createEnemy)
      history.push('/readme')
    } else {
      this.gameStart()
    }
  }

  componentWillUnmount () {
    clearInterval(this.state.createEnemy)
  }

  gameStart = () => {
    const {restartGame} = this.props

    restartGame()
    this.setState({createEnemy: this.generatorEnemy()})
  }

  generatorEnemy = () => {
    this.createNewEnemy()
    return setInterval(() => {
      if (!document.hasFocus()) return
      this.createNewEnemy()
    }, 3000)
  }

  createNewEnemy = () => {
    const {addNewEnemy, totalKill, enemys} = this.props
    const dynamicPosition = this.getDynamicPosition()
    const timestamp = Date.now()
    const dynamicEnemy = getRandomNumber(2)

    addNewEnemy(dynamicEnemy === 1 ? 'angle' : 'rectangle', {
      ...dynamicPosition,
      animation: {},
      life: dynamicEnemy,
      currentPosition: {...dynamicPosition},
      timestamp
    })

    if (totalKill !== 0 && totalKill % 5 === 0 && enemys.boss.length === 0) {
      addNewEnemy('boss', {
        timestamp: `${Date.now()}_boss`,
        top: 100,
        left: 100,
        life: 3,
        currentPosition: {
          x: 100,
          y: 100
        }
      })
    }
  }

  getDynamicPosition = () => {
    const {cubeRange} = this.state
    const appear = getRandomNumber(4)

    switch (appear) {
      case CONSTANT.APPEAR_POSITION.TOP:
        return {x: getRandomNumber(cubeRange.x), y: 0, appear}
      case CONSTANT.APPEAR_POSITION.LEFT:
        return {x: 0, y: getRandomNumber(cubeRange.y), appear}
      case CONSTANT.APPEAR_POSITION.BOTTOM:
        return {x: getRandomNumber(cubeRange.x), y: cubeRange.y, appear}
      case CONSTANT.APPEAR_POSITION.RIGHT:
        return {x: cubeRange.x, y: getRandomNumber(cubeRange.y), appear}
      default:
        return {}
    }
  }

  render () {
    const {
      classes,
      isGameOver,
      enemys,
      deleteEnemy,
      totalKill
    } = this.props
    const {cubeRange} = this.state

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              isGameOver &&
              <div className={classes.message}>
                <Typography
                  variant='display2'
                  align='center'
                  gutterBottom
                  style={{color: '#fff'}}>
                  擊墜數 {totalKill}
                </Typography>
                <div>
                  <Button variant='outlined' style={{color: '#fff'}} onClick={this.gameStart}>
                    再次挑戰
                  </Button>
                </div>
                <br />
                <div>
                  <Button
                    variant='outlined'
                    style={{color: '#fff'}}
                    component={Link}
                    to='/rank'>
                    看排行
                  </Button>
                </div>
                <br />
                <div>
                  <Button
                    variant='outlined'
                    style={{color: '#fff'}}
                    component={Link}
                    to='/readme'>
                    看說明
                  </Button>
                </div>
              </div>
            }
            {
              !isGameOver &&
              <div>
                <UserContent />
                <LifeContent />
                <KillContent />
                <div className={classes.bossRoad} />
                {
                  Object.keys(enemys).reduce((tempList, enemyKey) => {
                    let newEnemyList
                    switch (enemyKey) {
                      case 'angle':
                        newEnemyList = enemys[enemyKey].map(enemy => (
                          <AngleContent
                            enemy={enemy}
                            key={enemy.timestamp}
                            cubeRange={cubeRange}
                            deleteEnemy={deleteEnemy} />
                        ))
                        break
                      case 'rectangle':
                        newEnemyList = enemys[enemyKey].map(enemy => (
                          <RectangleContent
                            enemy={enemy}
                            key={enemy.timestamp}
                            cubeRange={cubeRange}
                            deleteEnemy={deleteEnemy} />
                        ))
                        break
                      case 'boss':
                        newEnemyList = enemys[enemyKey].map(enemy => (
                          <BossContent
                            enemy={enemy}
                            key={enemy.timestamp}
                            cubeRange={cubeRange}
                            deleteEnemy={deleteEnemy} />
                        ))
                        break
                      default:
                        newEnemyList = []
                        break
                    }
                    return tempList.concat(newEnemyList)
                  }, [])
                }
              </div>
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withRouter(withStyles(styles)(GameContent))



