import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import GameContent from '../component/Game/GameContent'

class Game extends Component {
  render () {
    return (
      <main>
        <GameContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    isDataLoaded: state.isDataLoaded,
    isGameOver: state.isGameOver,
    enemys: state.enemys,
    totalKill: state.totalKill,
    player: state.player
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    addNewButtle: (buttle) => dispatch(actionCreators.addNewButtle(buttle)),
    addNewEnemy: (key, enemy) => dispatch(actionCreators.addNewEnemy(key, enemy)),
    deleteEnemy: (key, enemy) => dispatch(actionCreators.deleteEnemy(key, enemy)),
    restartGame: () => dispatch(actionCreators.restartGame()),
    getRankList: () => dispatch(actionCreators.getRankList()),
    addNewRank: (rank) => dispatch(actionCreators.addNewRank(rank))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Game)



