import React, {Component} from 'react'
import { connect } from 'react-redux'
import { withStyles } from '@material-ui/core/styles'

import Typography from '@material-ui/core/Typography'

const styles = () => ({
  root: {
    position: 'fixed',
    bottom: '2rem',
    right: '2rem'
  },
  title: {
    color: '#fff'
  }
})

class KillContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const {classes, totalKill} = this.props

    return (
      <div className={classes.root}>
        <Typography variant='display1' component='p' className={classes.title}>
          擊墜數 <span>{totalKill}</span>
        </Typography>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    totalKill: state.totalKill
  }
}

export default connect(mapStateToProps)(withStyles(styles)(KillContent))



